import React, { useEffect, useState } from "react";
import "../UploadPage.css";

export default function UploadPage() {
  return (
    <>
      UploadPage
      <UploadBox />
    </>
  );
}

const UploadBox = () => {
  const [isActive, setActive] = useState(false);
  const [uploadedInfo, setUploadedInfo] = useState(null);

  //
  useEffect(() => {
    if (uploadedInfo) {
      console.log(uploadedInfo);
    }
  }, [uploadedInfo]);

  const handleDragStart = () => setActive(true);
  const handleDragEnd = () => setActive(false);
  //           
  const handleDragOver = (e) => {
    e.preventDefault();
  };
  //        drop 
  const handleDrop = (e) => {
    e.preventDefault();
    setActive(false);
    const file = e.dataTransfer.files[0];
    setFileInfo(file);
  };
  const handleUpload = ({ target }) => {
    const file = target.files[0];
    if (!file) {
      return;
    }
    setFileInfo(file);

    const formData = new FormData();
    //'file'    .
    formData.append("file", file);

    fetch("/upload_file", {
      method: "POST",
      body: formData,
    })
      .then((res) => {
        if (!res.ok) {
          throw new Error("Network response was not ok");
        }
        return res.json();
      })
      .then((data) => {
        console.log("Upload successful", data);
      })
      .catch((err) => {
        console.error("Error during file upload: ", err);
      });
  };

  //name, size, type uploadedInfo state 
  const setFileInfo = (file) => {
    const { name, size: byteSize, type } = file;
    const size = (byteSize / (1024 * 1024)).toFixed(2) + "mb";
    setUploadedInfo({ name, size, type });
  };

  const FileInfo = ({ uploadedInfo }) => (
    <ul className="preview_info">
      {Object.entries(uploadedInfo || { name: "", size: "", type: "" }).map(
        ([key, value]) => (
          <li key={key}>
            <span className="info_key">{key}</span>
            <span className="info_value">{value}</span>
          </li>
        )
      )}
    </ul>
  );

  return (
    //isActive   className 
    <label
      className={`preview${isActive ? " active" : ""}`}
      onDragEnter={handleDragStart}
      onDragOver={handleDragOver}
      onDragLeave={handleDragEnd}
      onDrop={handleDrop}
    >
      <input type="file" className="file" onChange={handleUpload}></input>
      {/* uploadedInfo    */}
      {uploadedInfo ? (
        <FileInfo {...uploadedInfo} />
      ) : (
        <>
          <p className="preview_msg">    .</p>
          <p className="preview_desc">  .</p>
        </>
      )}
    </label>
  );
};
